import React, { useState } from 'react';

import PlusIcon from './icons/Plus';
import MinusIcon from './icons/Minus';
import FadeInDownBounce from './Transitions/FadeInDownBounce';

import { colors, fonts, screen } from '../constants';

export default function Faqitem({
  question,
  answer,
  number,
  showExpandIcon = true,
  questionClassName,
  expandedQuestionClassName,
  onClick,
  children,
  ...props
}) {
  const [expanded, setExpanded] = useState(props.expanded);

  function onClickItem(event) {
    if (onClick) {
      onClick(event);
    } else {
      setExpanded(!expanded);
    }
  }

  if (expanded !== props.expanded) {
    setExpanded(props.expanded);
  }

  return (
    <div onClick={onClickItem}>
      <div className="faq-item__question">
        <div
          className={`faq-item__question_text ${
            expanded ? expandedQuestionClassName : questionClassName
          }`}
        >
          {number && (
            <span className="faq-item__question_number">{number}</span>
          )}
          {question}
        </div>
        {showExpandIcon && (
          <div className="faq-item__question_icon">
            {expanded ? <MinusIcon /> : <PlusIcon />}
          </div>
        )}
      </div>
      <FadeInDownBounce
        component="div"
        transitionAppear={true}
        active={expanded}
      >
        <div
          className={`faq-item__answer ${
            number ? 'faq-item__answer_number' : ''
          }`}
        >
          {children ? children : answer}
        </div>
      </FadeInDownBounce>
      <style jsx>{`
        .faq-item__question {
          cursor: pointer;
          font-size: 27px;
          font-weight: 400;
          line-height: 3.41;
          letter-spacing: 0.7px;
          align-items: center;
          justify-content: space-between;
          display: flex;
        }
        .faq-item__answer {
          max-width: 840px;
          font-size: 23px;
          font-weight: 400px;
          line-height: 1.65;
          letter-spacing: 0.6px;
          color: ${colors.faqItemAnswer};
          padding-bottom: 20px;
          text-align: left;
        }
        .faq-item__answer_number {
          padding-left: 64px;
        }
        .faq-item__question_text {
          padding-right: 30px;
        }
        .faq-item__question_number {
          width: 64px;
          display: inline-block;

          font-family: ${fonts.gilroy};
          font-size: 18px;
          font-weight: 600;
          letter-spacing: 0;

          text-align: center;
        }
        .faq-item__question_icon {
          display: flex;
          flex-direction: column;
          justify-content: center;
        }

        @media (max-width: ${screen.mobileMax}px) {
          .faq-item__question {
            font-size: 24px;
            line-height: 1.42;
            letter-spacing: 0.6px;
            padding: 15px 0;
          }
          .faq-item__answer {
            font-size: 22px;
            line-height: 1.64;
            letter-spacing: 0.8px;
          }
        }
      `}</style>
    </div>
  );
}
