import React from 'react';
import { graphql } from 'gatsby';

import Layout from '../components/Layout';
import SEO from '../components/Seo';
import Banner from '../components/Banner';
import Container from '../components/Container';
import BlockPrimaryHeader44 from '../components/BlockPrimaryHeader44';
import BlockHowItWorks from '../components/BlockHowItWorks';
import SpaceDivider from '../components/SpaceDivider';
import Block3PartIllo from '../components/Block3PartIllo';

import TimeIcon from '../components/icons/Time';
import ShieldIcon from '../components/icons/Shield';
import DropIcon from '../components/icons/Drop';

export const pageQuery = graphql`
  query HomePageQuery {
    allFile(filter: { relativeDirectory: { eq: "how-it-works" } }) {
      edges {
        node {
          childImageSharp {
            fluid {
              base64
              tracedSVG
              srcWebp
              srcSetWebp
              originalImg
              originalName
            }
          }
        }
      }
    }
  }
`;

const IndexPage = ({ data }) => {
  return (
    <Layout>
      <SEO title="Grow your b2b sales with automated credit terms | Resolve" />
      <Banner showImage />
      <Container>
        <BlockPrimaryHeader44
          desc="Resolve is the easiest way to increase your customers' buying power via credit lines and Net Payment Terms. Trusted by the world's most innovative companies. "
          primaryButtonText="Get a Demo"
          primaryButtonTo="/"
        />
        <BlockHowItWorks
          title="Resolve fits your b2b sales flow"
          subtitle="simple and automated"
          data={[
            {
              id: '01',
              question: 'Offer net terms',
              answer:
                'Easily extend net terms financing on your website using our net terms payment gateway.',
              image:
                data.allFile.edges[0].node.childImageSharp.fluid.originalImg,
            },
            {
              id: '02',
              question: 'Get more customers',
              answer:
                'Buyers apply through a simple application and get approved for dedicated payment terms (net 30-60) within one business day.',
              image:
                data.allFile.edges[1].node.childImageSharp.fluid.originalImg,
            },
            {
              id: '03',
              question: 'Make sales',
              answer:
                'You receive more sales and your buyers receive a personalized confirmation of their payment.',
              image:
                data.allFile.edges[2].node.childImageSharp.fluid.originalImg,
            },
            {
              id: '04',
              question: 'Get paid',
              answer:
                'After a purchase is placed, Resolve automatically transfers the funds to your bank account. We handle customer invoicing, billing, and collections.',
              image:
                data.allFile.edges[3].node.childImageSharp.fluid.originalImg,
            },
          ]}
        />
        <SpaceDivider height="100" />
        <Block3PartIllo
          title="Why sell with Resolve"
          rows={[
            [
              {
                icon: <TimeIcon />,
                title: 'Expand average order size',
                content: (
                  <div>
                    Give your buyers the credit they need to expand their
                    budgets, allowing them to pull the trigger on even larger
                    purchases.
                  </div>
                ),
              },
              null,
              null,
            ],
            [
              {
                icon: <ShieldIcon width="28" height="28" />,
                title: 'Increase repeat purchases',
                content: (
                  <div>
                    Provide your buyers with a dedicated line of credit,
                    encouraging them to purchase with you again and again.
                  </div>
                ),
              },
              {
                icon: <DropIcon width="28" height="28" />,
                title: 'Convert new and old leads into buyers',
                content: (
                  <div>
                    Our credit approvals help you reach out to new or existing
                    customers who haven't started purchasing yet.{' '}
                  </div>
                ),
              },
              null,
            ],
          ]}
          backgroundImage="/static/images/banners/why-sell-banner.png"
        />
        <SpaceDivider height="150" />
      </Container>
    </Layout>
  );
};

export default IndexPage;
