import React from 'react';
import { CSSTransition, transit } from 'react-css-transition';

const easeOutQuart = 'cubic-bezier(0.165, 0.84, 0.44, 1)';
const easeInQuad = 'cubic-bezier(0.55, 0.085, 0.68, 0.53)';

CSSTransition.childContextTypes = {};

export default function FadeInDown({
  distance = '20px',
  duration = 250,
  children,
  ...props
}) {
  const transitionStyles = {
    defaultStyle: {
      opacity: 0,
      transform: `translate(0, -${distance})`,
      display: 'none',
    },
    enterInitStyle: {
      opacity: 0,
      display: 'block',
      transform: `translate(0, -${distance})`,
    },
    enterStyle: {
      opacity: transit(1, duration, 'linear'),
      transform: transit(`translate(0, 0)`, duration, easeOutQuart),
    },
    leaveStyle: {
      opacity: transit(0, duration, 'linear'),
      transform: transit(`translate(0, -${distance})`, duration, easeInQuad),
    },
    activeStyle: {},
    active: true,
    transitionAppear: true,
  };
  return (
    <CSSTransition {...transitionStyles} {...props}>
      {children}
    </CSSTransition>
  );
}
